body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.eachEpisode{
    padding: 23.5px !important;
    display:flex;
    align-items:center;
    padding:23px !important;
    background:black; 
    color:white;
}
.eachEpisode:hover{
    background:url('/assets/blackMetal1.jpg') center center ;
    color: white !important;
    cursor: pointer !important;
}
.buttonWatchShow{
    background: url('/assets/buttonMetal.jpg') !important;
    color: black !important;
    cursor: pointer !important;
}
.buttonWatchShow:hover{
    background: black !important;
    color: gold !important;
    cursor: pointer !important;
}
.buttonWatchHide{
    background: url('/assets/buttonMetal.jpg') !important;
    color: black !important;
    cursor: pointer !important;    
}
.buttonWatchHide:hover{
    background: black !important;
    color: gold !important;
    cursor: pointer !important;
}
.eachEpisodeWrapper{
    background:white;
    width:600px;
    border-left:5px solid gold;
    background:gold;
    position: relative;
}
.buttonWatchWrapper{
    position: absolute;
    right:5px
}
.searchMovieInput{
    width: 500px;
    background: rsgb(255, 255, 255, 0.5);
    color:black;
    border: none;
    outline: none;
    height: 40px;
    border-radius: 10px;
    padding-left: 10px;
    font-size: 20px;
    margin-top: 0px;
    margin-bottom: 50px;
}
.movieItemWrapper{
    width: 300px;
    border: 2px solid black;
    height: 400px;
    overflow: hidden;
}
.movieItem{
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    color:white;
    background: black;
    text-align: center;
    object-fit: contain;
}

.imagePoster{
    width: 300px;
    height: 400px;
}
.imagePoster{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    transition: .5s ease;
    background-color: black;
}
.imageMoviesWrapper{
    position: relative;
}
.imageMoviesWrapper:hover .imagePoster{
    opacity: 1;
}
.hoverMovieDetails{
    position: absolute;
    width: 90%;
    left: 50%;
    top: 45%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    text-align: center;
    opacity: 0;
}
.imageMoviesWrapper:hover .hoverMovieDetails{
   opacity: 1;
}
.innerBorder{
    position: absolute;
    top:10px;
    left:7px;
    right:10px;
    bottom: 65px;
    border: 5px solid wheat;
    border-style: inset;
    opacity: 0;
    width: 276px;
    height: 370px;
}
.imageMoviesWrapper:hover .innerBorder{
    opacity: 1;
 }
.movieTitle{
    width: 87.5%;
    margin-top: 60px;
    color:white;
    background: rgb(0,0,0, 0.8);
    -webkit-clip-path: polygon(0 0, 80% 0, 100% 100%, 0% 100%);
            clip-path: polygon(0 0, 80% 0, 100% 100%, 0% 100%);
    display: flex;
    justify-content: flex-start;
}
.movieHeaderWrapper{
    display: flex;
    justify-content:flex-start;
}
.imageMovieWrapper{
    float: left;
    padding-right: 50px;
}
.movieMainDiv{
    width: 100%;
    height: 100vh;
    overflow: scroll;
}
.movieOverviewWrapper{
   background: rgb(0, 0, 0, 0.5);
   position: relative;
   width: 100%;
   height: 450px;
   overflow: hidden; 
   width: 75%;
}
.movieOverview{
    padding: 20px;
    color: antiquewhite;
}
.genreButton{
    color:white;
    margin-right:15px;
}
.dv-star-rating{
    font-size: 24px !important;
}
.trailerFont{
    width: 100%;
    -webkit-clip-path: polygon(15% 40%, 5% 28%, 0 0, 85% 0%, 100% 0, 100% 0, 94% 13%, 92% 26%, 78% 41%, 71% 25%, 51% 43%, 23% 22%);
            clip-path: polygon(15% 40%, 5% 28%, 0 0, 85% 0%, 100% 0, 100% 0, 94% 13%, 92% 26%, 78% 41%, 71% 25%, 51% 43%, 23% 22%);    
    font-size:24px;
    color:white;
    text-align:center ;
    background:rgb(0,0,0,0.8) ;
    padding:10px;
    padding-bottom: 200px;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    font-family: Righteous, cursive;
    position: absolute;
    top:0;
    left: 0;

}
.castMovieCrew {
    -webkit-clip-path: polygon(0% 0%, 100% 0%, 100% 75%, 100% 75%, 50% 40%, 0% 75%);
            clip-path: polygon(0% 0%, 100% 0%, 100% 75%, 100% 75%, 50% 40%, 0% 75%);
    background:rgb(0,0,0,0.8) ;
    padding-bottom: 50px;
    height: 100px;
}
.castMovieCrewFont{
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    font-family: Righteous, cursive;
    font-size:25px;
    color:white;
    text-align:center ;
    -webkit-clip-path: polygon(0% 15%, 15% 15%, 15% 0%, 85% 0%, 85% 15%, 100% 15%, 100% 85%, 85% 85%, 85% 100%, 15% 100%, 15% 85%, 0% 85%);
            clip-path: polygon(0% 15%, 15% 15%, 15% 0%, 85% 0%, 85% 15%, 100% 15%, 100% 85%, 85% 85%, 85% 100%, 15% 100%, 15% 85%, 0% 85%);
    background: rgb(0, 0, 0, 0.5);
    width: 600px;
    height: 100px;
    margin: 0 auto;
}
.castMovieCrewShape{
    height: 100px;
}
